<template>
  <div class="share">
    <p class="diggit" @click="praiseBlog(blogUid)">
      <a href="javascript:void(0);"></a>
      <span v-if="praiseCount!= 0">
        (<b id="diggnum">{{praiseCount}}</b>)
      </span>
    </p>
    <p class="dasbox">
      <a href="javascript:void(0)" @click="dashangToggle()" class="dashang" title=""></a>
    </p>
    <div class="hide_box" v-if="showPay"></div>
    <div class="shang_box" v-if="showPay">
      <a class="shang_close" href="javascript:void(0)" @click="dashangToggle()" title=""></a>
      <div class="shang_tit">
        <p>!</p>
      </div>
      <div class="shang_payimg">
        <img :src="payCode" alt="" title="">
      </div>
      <div class="pay_explain"></div>
      <div class="shang_payselect">
        <div
          class="pay_item"
          :class="[this.payMethod==1?'checked':'']"
          data-id="alipay"
          @click="choosePay('1')"
        >
          <span class="radiobox"></span>
          <span class="pay_logo">
            <img src="../../../static/images/alipay.jpg" alt="">
          </span>
        </div>
        <div
          class="pay_item"
          :class="[this.payMethod==2?'checked':'']"
          data-id="weipay"
          @click="choosePay('2')"
        >
          <span class="radiobox"></span>
          <span class="pay_logo">
            <img src="../../../static/images/wechat.jpg" alt="">
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getWebConfig } from "../../api/index";
import {
  praiseBlogByUid,
  getBlogPraiseCountByUid
} from "../../api/blogContent";
import {mapMutations} from "vuex";
export default {
  name: "PayCode",
  props: {
    praiseCount: {
      type: Number,
      default: 0
    },
    blogUid: {
      type: String
    }
  },
  data() {
    return {
      webConfigData: [],
      showPay: false, //
      payMethod: 1, // 1:   2
      payCode: "", //
    };
  },
  created() {
    getWebConfig().then(response => {
      console.log("")
      if (response.code == this.$ECode.SUCCESS) {
        this.webConfigData = response.data;
        this.payCode = this.webConfigData.aliPayPhoto;
      }
    });
  },
  methods: {
    //vuex
    ...mapMutations(['setLoginMessage']),
    dashangToggle: function() {
      this.showPay = !this.showPay;
    },
    // 
    choosePay: function(type) {
      this.payMethod = type;
      if (type == 1) {
        this.payCode = this.webConfigData.aliPayPhoto;
      } else {
        this.payCode = this.webConfigData.weixinPayPhoto;
      }
    },
    //
    praiseBlog: function(uid) {
      // 
      let isLogin = this.$store.state.user.isLogin
      if(!isLogin) {
        this.$notify.error({
          title: '',
          message: '~',
          offset: 100
        });
        // 
        this.setLoginMessage(Math.random())
        return;
      }

      var params = new URLSearchParams();
      params.append("uid", uid);
      praiseBlogByUid(params).then(response => {
        if (response.code == this.$ECode.SUCCESS) {
          this.$notify({
            title: '',
            message: "",
            type: 'success',
            offset: 100
          });
          this.$emit('update:praiseCount',response.data);
        } else {
          this.$notify.error({
            title: '',
            message: response.message,
            offset: 100
          });
        }
      });
    },
    //
    getPraiseCount: function(uid) {
      var params = new URLSearchParams();
      params.append("uid", uid);
      getBlogPraiseCountByUid(params).then(response => {
        if (response.code == this.$ECode.SUCCESS) {
          this.praiseCount = response.data;
        }
      });
    }
  }
};
</script>

<style>
  .diggit {
    cursor: pointer;
  }
</style>



